import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import articlesData from './articulos.json'; // Asegrate de que la ruta al archivo JSON sea correcta.

function ArticleBody() {
  const { id } = useParams();
  const [article, setArticle] = useState(null);

  useEffect(() => {
    // Filtrar los datos del artculo basndose en la ID obtenida de la URL.
    // Asegrate de que los artculos en tu archivo JSON tengan un campo 'id' o cambia este campo por el que utilices.
    const foundArticle = articlesData.find(article => article.id === parseInt(id));
    setArticle(foundArticle);
  }, [id]);

  // Comprobacin en caso de que no se encuentre el artculo.
  if (!article) {
    return <div>Artculo no encontrado.</div>;
  }

  return (
    <div>
      {/* Usa un margen directamente en el estilo si es necesario, pero considera usar clases de CSS para una mejor prctica. */}
      <div style={{marginTop: '100px'}}>
        <h2>{article.title}</h2>
        <img src={article.image} alt={article.title} />
        <p>{article.body}</p>
      </div>
    </div>
  );
}

export default ArticleBody;